import React, { useEffect } from "react";

import { UserProps } from "../../types";
import { getFormattedDate } from "../../utils";
import * as S from "./style";

type IssueItemProps = {
  number: number;
  title: string;
  user: UserProps;
  createdAt: string;
  comments: number;
  onClick?: () => void;
};

export const IssueItem = ({
  number,
  title,
  user,
  createdAt,
  comments,
  onClick,
}: IssueItemProps) => {
  return (
    <S.ItemContainer onClick={onClick}>
      <S.IssueItemBody>
        <S.IssueItemTitle>{`#${number} ${title}`}</S.IssueItemTitle>
        <S.IssueItemInfo>
          <S.IssueItemProfileImg src={`${user.avatar_url}`} />
          <S.IssueItemDescBlock>
            <S.IssueItemDesc>{`: ${user.login}`}</S.IssueItemDesc>
            <S.IssueItemDesc>{`: ${getFormattedDate(
              createdAt
            )}`}</S.IssueItemDesc>
          </S.IssueItemDescBlock>
        </S.IssueItemInfo>
      </S.IssueItemBody>
      <S.IssueItemComments>{`: ${comments}`}</S.IssueItemComments>
    </S.ItemContainer>
  );
};
